<script>
import http from "../http-common";

export default {
  name: "RegisterForm",
  data() {
    return {
      nom: "",
      prenom: "",
      mail: "",
      password: "",
      dateDeNaissance: Date,
      country: "",
      city: "",
      departement: "",
      local: [],
      urlImage: "",
      subscription: 'none',
      numElecteur: null,
      autorisedElections: []
    };
  },
  mounted() {
    if (localStorage.getItem('reloaded')) {
      // The page was just reloaded. Clear the value from local storage
      // so that it will reload the next time this page is visited.
      localStorage.removeItem('reloaded');
      let recaptchaScript = document.createElement('script')
      recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
      document.head.appendChild(recaptchaScript)
    } else {
      // Set a flag so that we know not to reload the page twice.
      localStorage.setItem('reloaded', '1');
      location.reload();
    }
  },
  methods:{
    register(){
      const captcha = document.querySelector('#g-recaptcha-response').value;
      http
          .post("/user/verifyCaptcha", JSON.stringify({captcha}))
          .then(res => {
            if (res.data.success){
              this.local.push(this.country, this.city, this.departement)
              const newUser = {
                nom: this.nom,
                prenom: this.prenom,
                mail: this.mail,
                password: this.password,
                dateDeNaissance: this.dateDeNaissance,
                local: this.local,
                urlImage: this.urlImage,
                subscription: this.subscription,
                numElecteur: this.numElecteur,
                autorisedElections: this.autorisedElections
              };
              http
                  .post("/user/register", newUser)
                  .then(response => {
                    this.$store.state.actualClient = response.data
                    this.$router.push('DifferentAcount')
                  })
                  .catch(e => {
                    if (e.response.status === 500){
                      alert("One or many values are already used")
                    }
                    console.log(e);
                  });
            }
            else {
              alert("Captcha not verify")
            }
          })
      //.catch() pas d'erreur, on ne renvoit que des fichiers Json
    }
  },
}
</script>

