<script>
import http from "../http-common";

export default {
  name: "LoginForm",
  data() {
    return {
      mail: "",
      password: ""
    };
  },
  mounted() {
    if (localStorage.getItem('reloaded')) {
      // The page was just reloaded. Clear the value from local storage
      // so that it will reload the next time this page is visited.
      localStorage.removeItem('reloaded');
      let recaptchaScript = document.createElement('script')
      recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
      document.head.appendChild(recaptchaScript)
    } else {
      // Set a flag so that we know not to reload the page twice.
      localStorage.setItem('reloaded', '1');
      location.reload();
    }
  },
  methods:{
    getAge(dateString)
      {
        var today = new Date();
        var birthDate = new Date(dateString);
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate()))
        {
          age--;
        }
        return age;
      },
    //login avec verification : captcha, jwt, mdp
    login() {
      const captcha = document.querySelector('#g-recaptcha-response').value;
      http
          .post("/user/verifyCaptcha", JSON.stringify({captcha}))
          .then(res => {
            if (res.data.success){
              const User = {
                mail: this.mail,
                password: this.password,
              };
              http
                  .post("/user/login", User)
                  .then(response => {
                    this.$store.state.actualClient = response.data
                    if (this.getAge(this.$store.state.actualClient.dateDeNaissance.toString().substring(0, 10)) >= 18){

                      http.get("election/getElection").then((r) => {
                            let tab = r.data;
                            tab.forEach((res) => {
                              if (res.type === 'officiel'){
                                  const newElection = {
                                    election: res._id,
                                    bool: false
                                  }
                                let arrTest = this.$store.state.actualClient.autorisedElections.filter(o => o.election === res._id)
                                if (arrTest.length === 0){
                                    this.$store.state.actualClient.autorisedElections.push(newElection);
                                    http.put("user/joinVote", this.$store.state.actualClient)
                                }
                                  }
                                })}
                      )
                      if (this.$store.state.actualClient.numElecteur == null){this.$router.push('IfMajeurVue')}
                      else {this.$router.push('/HomePageVue')}

                    }
                    else {
                      this.$router.push('HomePageVue')
                    }
                  })
                  .catch(e => {
                    if (e.response.status === 401){
                      console.log(e);
                      alert("Wrong email")
                    }
                    else if (e.response.status === 402){
                      console.log(e);
                      alert("No access to this web app")
                    }
                    else {
                      console.log(e);
                      alert("Wrong password")
                    }
                  });
            }
            else{
              alert("Captcha not verify")
            }
          })
      //.catch() pas d'erreur, on ne renvoit que des fichiers Json
    },
  },
}
</script>

